/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 *
 */

let baseUrl = '';

if (process.env.NODE_ENV == 'development') {
    baseUrl = '';
    // routerMode = 'history';
}else if(process.env.NODE_ENV == 'production'){
  baseUrl = '101.37.27.156：7081';
}

export default {
	baseUrl:baseUrl,
}



